	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		// Init DataSource
		this.dataSource = new MatTableDataSource();

		this.credentialsSubscription = this.store.select('credentials').subscribe(state => {
			this.token = state.authToken;
			this.headers = new HttpHeaders({ Authorization: 'Bearer ' + this.token, responseType: 'text' });
			this.requestOptions = {
				headers: this.headers,
				// responseType: 'text'
			};
		});

		this.subscriptions.push(this.credentialsSubscription);

		this.loadFinancialList();
	}

	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	arabicRangeLabel = (page: number, pageSize: number, length: number) => {
		if (length === 0 || pageSize === 0) {
			return `0 من ${length}`;
		}

		length = Math.max(length, 0);

		const startIndex = page * pageSize;

		// If the start index exceeds the list length, do not try and fix the end index to the end.
		const endIndex = startIndex < length ?
			Math.min(startIndex + pageSize, length) :
			startIndex + pageSize;

		return `${startIndex + 1} - ${endIndex} من ${length}`;
	};


	/** ACTIONS */
	/**
 * Load Financial List from service through data-source
 */
	loadFinancialList() {
		this.isLoadingResults = true;
		if (this.selectedDate.includes('-')) {
			this.month = this.selectedDate.split('-')[1];
			this.year = this.selectedDate.split('-')[0];
		}
		let url = '?month=' + this.month + '&year=' + this.year;

		this.http.get(
			globalVariables.baseUrl + 'api/Reports/Monthly/Financial' + url,
			{ headers: this.headers }
		)
			.subscribe((res: any) => {
				if (res.success) {

					this.isLoadingResults = false;
					this.totalCashBalance = res.data.totalCashBalance;
					this.discounts = res.data.discounts;
					this.expenses = res.data.expenses;
					let data = [];
					for (let index = 0; index < res.data.categoryFinancialReports.length; index++) {
						const item = res.data.categoryFinancialReports[index];
						data.push({
							...item,
						});
					}

					this.dataSource = new MatTableDataSource(data);

					if (this.paginator) {
						this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
						this.paginator._intl.firstPageLabel = this.languageText.firstPage;
						this.paginator._intl.previousPageLabel = this.languageText.previousPage;
						this.paginator._intl.nextPageLabel = this.languageText.nextPage;
						this.paginator._intl.lastPageLabel = this.languageText.lastpage;

						this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
					}

					this.dataSource.paginator = this.paginator;
					this.dataSource.sort = this.sort;
				}
			}, (err) => {
				this.isLoadingResults = false;

				this.dataSource.data = [];

				if (this.paginator) {
					this.paginator._intl.itemsPerPageLabel = this.languageText.itemsPerPage;
					this.paginator._intl.firstPageLabel = this.languageText.firstPage;
					this.paginator._intl.previousPageLabel = this.languageText.previousPage;
					this.paginator._intl.nextPageLabel = this.languageText.nextPage;
					this.paginator._intl.lastPageLabel = this.languageText.lastpage;

					this.paginator._intl.getRangeLabel = this.arabicRangeLabel;
				}

				this.dataSource.paginator = this.paginator;
				this.dataSource.sort = this.sort;
			});
	}


